        flex-direction: column;
      }
    }
    
    .gallery-thumbnails {
      grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    }
  }
  
  .image-input {
    flex-direction: column;
  }
  
  .work-tabs {
    .tab-button {
      padding: 8px 12px;
      font-size: 14px;
    }
  }
